
import { Vue } from 'vue-class-component'

export default class Home extends Vue {
  // 当前选中
  current: string[] = ['user']
  // 菜单点击
  handleClick(e: {key: string; [key: string]: unknown}) {
    this.$router.push(e.key)
  }

  mounted() {
    const name = this.$route.name?.toString().toLowerCase()
    if (name) {
      this.current = [name]
    }
  }
}
